<template>
    <el-container v-loading="loading">
        <div class="rise x add">
            <div class="title">{{title}}</div>
            <div class="content">
                <div class="item" v-for="(item, index) in data" :key="index">
                    <span>{{item.name}}</span>
                    <!-- input -->
                    <div v-if="!item.type">
                        <el-input v-if="item.module === 'name'" v-model="name" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'account'" v-model="account" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'password'" v-model="password" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'email'" v-model="email" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'mobile'" v-model="mobile" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'score'" v-model="score" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'memo'" v-model="memo" :placeholder="item.placeholder"/>
                    </div>

                    <!-- select -->
                    <div v-if="item.type === 'select'">
                        <el-select v-if="item.module === 'schoolId'" v-model="schoolId" placeholder="全部">
                            <el-option
                                v-for="item in item.options"
                                :key="item.id"
                                :label="item.dValue"
                                :value="item.id"
                            ></el-option>
                        </el-select>
                        <el-select v-if="item.module === 'classId'" v-model="classId" placeholder="全部">
                            <el-option
                                v-for="item in item.options"
                                :key="item.id"
                                :label="item.dValue"
                                :value="item.id"
                            ></el-option>
                        </el-select>
                        <el-select v-if="item.module === 'roleId'" v-model="roleId" placeholder="全部">
                            <el-option
                                v-for="item in item.options"
                                :key="item.id"
                                :label="item.dValue"
                                :value="item.id"
                            ></el-option>
                        </el-select>
                    </div>
                </div>
            </div>
            <div class="btn-list">
                <el-button @click="cancle()" class="btn">取消</el-button>
                <el-button @click="save()" class="btn">保存</el-button>
            </div>
        </div>
    </el-container>
</template>

<script>
import queryString from "querystring";
import { DateDay } from "utils/formData";
import { getRoles, getSchool, getClass } from "data/listBody";
import md5 from "md5";
const titles = ["新建", "编辑"];
const tips = ['请补全信息后提交'];
const defaultV = [
  {
    name: "校区：",
    placeholder: "请选择校区",
    module: "schoolId",
    type: "select"
  },
  {
    name: "班级：",
    placeholder: "请选择班级",
    module: "classId",
    type: "select"
  },
  { name: "用户姓名：", placeholder: "请输入用户姓名", module: "name" },
  { name: "用户账号：", placeholder: "请输入用户账号", module: "account" },
  { name: "用户手机：", placeholder: "请输入用户手机", module: "mobile" },
  {
    name: "所属角色：",
    placeholder: "请选择所属角色",
    module: "roleId",
    type: "select"
  }
];

export default {
  name: "systemUseradd",
  data() {
    return {
      title: "",
      id: "",
      data: defaultV,
      loading: false,
      name: "",
      account: "",
      schoolId: "",
	  email: "",
	  mobile:"",
      score: "",
      classId: "",
      memo: "",
      roleId: ""
    };
  },
  mounted() {
    this.dropInit();
    this.dataInit();
  },
  methods: {
    dropInit() {
        // 数据初始化
        getSchool(res => {
            let leftMenu = this.data.map(item => {
            if (item.module === "schoolId") item.options = res;
                return item;
            });
            this.data = leftMenu;
        }, "filter");
        getClass(res => {
            let leftMenu = this.data.map(item => {
            if (item.module === "classId") item.options = res;
                return item;
            });
            this.data = leftMenu;
        }, "filter");
        getRoles( res => {
            let leftMenu = this.data.map(item => {
            if (item.module === "roleId") item.options = res;
                return item;
            });
            this.data = leftMenu;
        }, "filter", "less");
    },
    dataInit() {
		const { type, id } = this.$route.query;
		this.type = type;
		this.id = id;
		if (type !== "edit") return (this.title = titles[0]);
		this.title = titles[1];
		this.detailAjax();
    },
    editInit(res) {
		const { name, account, password, schoolId, classId, mobile, email, memo, roleId } = res.data;
		this.name = name;
		this.account = account;
		this.password = password;
		this.schoolId = schoolId;
		this.classId = classId;
		this.mobile = mobile;
		this.email = email;
		this.memo = memo;
		this.roleId = roleId;
    },
    detailAjax() {
        //查看详情
		this.loading = true;
		this.api.get(`/user/${this.id}`).then(res => {
			this.loading = false;
			this.editInit(res);
		})
		.catch(err => {
			console.log(err);
			this.loading = false;
			this.$message(err.message);
		});
    },
    editAjax() {
      if (!this.account || !this.schoolId || !this.classId  || !this.roleId) return this.$message(tips[0]);
      const params = {
        name: this.name,
        account: this.account,
        password: md5(this.account).toLowerCase(),
        schoolId: this.schoolId,
        classId: this.classId,
        mobile: this.mobile,
        email: this.email,
        memo: this.memo,
        roleId: this.roleId
      };
      const axiosType = ["post", "put"];
      let type = axiosType[0];
      if (this.type === "edit") {
        params.id = this.id;
        type = axiosType[1];
      }
      this.loading = true;
      this.api[type](`/user`, params, "form").then(res => {
          this.loading = false;
          this.$router.go(-1);
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
          this.$message(err.message);
        });
    },
    save() {
      this.editAjax();
    },
    cancle() {
      //取消
      this.$router.go(-1);
    }
  }
};
</script>
 
<style lang="less" scoped>
</style>
 